import React, { useState } from "react";
import { Button, Col, Form, Row } from "@themesberg/react-bootstrap";
import axiosInstance from "../axiosConfig";

const ForgotPassword = () => {
  const [email, setEmail] = useState("");
  const [success, setSuccess] = useState("");
  const [error, setError] = useState("");

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!email) return;
    try {
      await axiosInstance.post("/auth/forgot-password", {
        email,
      });
      setSuccess("Password reset link has been sent.");
    } catch (err) {
      console.log(err);
      setError("An error has occured");
    }
  };

  return (
    <div style={{ background: "#262b40" }}>
      <div
        className="d-flex justify-content-center px-2 align-items-center"
        style={{ height: "100vh", width: "100vw", backgroundSize: "cover" }}
      >
        <div style={{ width: "400px", maxWidth: "100%", color: "orange" }}>
          <h2 style={{ color: "darkorange" }}>Forgot Password</h2>
          <Form onSubmit={handleSubmit} className="mt-3">
            <Row>
              <Col xs={12}>
                <Form.Group>
                  <Form.Label>Email</Form.Label>
                  <Form.Control
                    type="email"
                    required
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                  />
                </Form.Group>
              </Col>
            </Row>
            <Row className="mt-5">
              <Col xs={12}>
                <Button
                  style={{
                    width: "100%",
                    background: "linear-gradient(to right, #fb7b4a, #ee3344)",
                  }}
                  type="submit"
                >
                  Send Forgot Password Link
                </Button>
              </Col>
            </Row>
          </Form>
          <div className="mt-3">
            {success && <p style={{ color: "green" }}>{success}</p>}
            {error && <p style={{ color: "red" }}>{error}</p>}
          </div>
        </div>
      </div>
    </div>
  );
};

export default ForgotPassword;
